/* Home.css - Professional Truck Industry Design */

/* Import industrial fonts */
@import url('https://fonts.googleapis.com/css2?family=Rajdhani:wght@300;400;500;600;700&family=Orbitron:wght@400;500;600;700;900&display=swap');

/* CSS Variables */
:root {
  --primary-bg: #1a1d29;
  --secondary-bg: #252839;
  --dark-bg: #1a1a1a;
  --accent-gold: #d4af37;
  --accent-gold-dark: #b8941f;
  --accent-gold-light: rgba(212, 175, 55, 0.1);
  --text-dark: #1a1a1a;
  --text-light: #ffffff;
  --text-muted: #9ca3af;
  --border-color: #374151;
  --shadow-light: 0 4px 20px rgba(0, 0, 0, 0.08);
  --shadow-medium: 0 8px 30px rgba(0, 0, 0, 0.12);
  --shadow-heavy: 0 12px 40px rgba(0, 0, 0, 0.15);
  --border-radius: 12px;
  --border-radius-lg: 20px;
  --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Global styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Rajdhani', sans-serif;
  background: var(--primary-bg);
  color: var(--text-light);
  line-height: 1.6;
  overflow-x: hidden;
}

/* Truck-themed decorative elements */
@keyframes truckDrive {
  0% { transform: translateX(-100px); }
  100% { transform: translateX(calc(100vw + 100px)); }
}

@keyframes enginePulse {
  0%, 100% { opacity: 0.3; transform: scale(1); }
  50% { opacity: 0.6; transform: scale(1.05); }
}

@keyframes slideInLeft {
  0% { 
    transform: translateX(-100%); 
    opacity: 0; 
  }
  100% { 
    transform: translateX(0); 
    opacity: 1; 
  }
}

@keyframes slideInRight {
  0% { 
    transform: translateX(100%); 
    opacity: 0; 
  }
  100% { 
    transform: translateX(0); 
    opacity: 1; 
  }
}

@keyframes fadeInUp {
  0% {
    transform: translateY(30px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Home container */
.home {
  min-height: 100vh;
  background: var(--primary-bg);
  position: relative;
}

/* Industrial side panels */
.home::before {
  left: 0;
  border-right: 2px solid var(--accent-gold-dark);
}

.home::after {
  right: 0;
  border-left: 2px solid var(--accent-gold-dark);
}

/* Hero Section with Volvo Background - Enhanced visibility */
.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  position: relative;
  background: 
    linear-gradient(135deg, rgba(26, 29, 41, 0.4) 0%, rgba(26, 29, 41, 0.3) 50%, rgba(26, 29, 41, 0.4) 100%),
    url('../pages/assets/volvo.jpg') center center/cover no-repeat;
  background-attachment: fixed;
}

.hero-content {
  max-width: 1000px;
  text-align: center;
  z-index: 2;
  position: relative;
  animation: fadeInUp 1s ease-out;
  margin-top: 30vh;
  background: transparent;
  backdrop-filter: none;
}

.hero-content p {
  font-size: 1.3rem;
  color: var(--text-light);
  margin-bottom: 3rem;
  font-weight: 400;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
}

/* Info Sliders - Now pinned to viewport */
.info-slider {
  position: absolute;
  top: 20vh;
  width: 350px;
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(212, 175, 55, 0.3);
  border-radius: var(--border-radius-lg);
  padding: 2rem;
  z-index: 1;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  transition: var(--transition);
}

.info-slider.left {
  left: 2rem;
  animation: slideInLeft 1s ease-out 0.5s both;
}

.info-slider.right {
  right: 2rem;
  animation: slideInRight 1s ease-out 0.7s both;
}

.info-slider:hover {
  transform: scale(1.02);
  background: rgba(37, 40, 57, 0.7);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.3);
}

.info-slider::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    var(--accent-gold) 50%, 
    transparent 100%);
  border-radius: var(--border-radius-lg) var(--border-radius-lg) 0 0;
}

.info-slider h3 {
  font-family: 'Orbitron', monospace;
  font-size: 1.5rem;
  color: var(--accent-gold);
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
}

.info-slider .spec-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: 0.5rem;
}

.info-slider .spec-item:last-child {
  border-bottom: none;
  margin-bottom: 0;
}

.info-slider .spec-label {
  font-weight: 600;
  color: var(--text-light);
  font-size: 1rem;
}

.info-slider .spec-value {
  font-weight: 700;
  color: var(--accent-gold);
  font-size: 1rem;
}

.info-slider .highlight {
  background: rgba(212, 175, 55, 0.15);
  padding: 1rem;
  border-radius: var(--border-radius);
  margin-top: 1rem;
  text-align: center;
  border: 1px solid rgba(212, 175, 55, 0.3);
}

.info-slider .highlight .feature {
  color: var(--accent-gold);
  font-weight: 700;
  font-size: 1.1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Professional Search Form - Completely transparent background */
.search-form {
  background: transparent;
  backdrop-filter: none;
  border: none;
  border-radius: var(--border-radius-lg);
  padding: 2.5rem;
  margin-bottom: 3rem;
  box-shadow: none;
  transition: var(--transition);
  position: relative;
  overflow: hidden;
  margin-top: 2rem;
}

.search-form::before {
  display: none;
}

.search-form:hover {
  box-shadow: none;
  transform: none;
  border-color: transparent;
  background: transparent;
}

/* Search Container */
.search-container {
  display: flex;
  gap: 1rem;
  align-items: center;
  margin-bottom: 2rem;
}

/* Search Input Styling */
.search-input-wrapper {
  position: relative;
  flex: 1;
}

.search-icon {
  position: absolute;
  left: 1.25rem;
  top: 50%;
  transform: translateY(-50%);
  color: var(--accent-gold);
  font-size: 1.1rem;
  z-index: 2;
  transition: var(--transition);
}

.search-input-wrapper.focused .search-icon {
  color: var(--accent-gold);
}

.search-input-wrapper input {
  width: 100%;
  padding: 1.25rem 3.5rem 1.25rem 3.5rem;
  background: rgba(26, 29, 41, 0.8);
  border: 2px solid var(--accent-gold);
  border-radius: var(--border-radius);
  color: var(--text-light);
  font-size: 1.1rem;
  font-weight: 500;
  font-family: 'Rajdhani', sans-serif;
  transition: var(--transition);
  outline: none;
  height: 60px;
}

.search-input-wrapper input::placeholder {
  color: var(--text-muted);
  font-style: italic;
}

.search-input-wrapper input:focus {
  border-color: var(--accent-gold);
  background: rgba(26, 29, 41, 0.9);
  box-shadow: 0 0 0 3px var(--accent-gold-light);
}

/* Industrial Button Design */
.search-type-buttons {
  display: flex;
  gap: 0;
  align-items: center;
  background-color: #0f1a2c00 !important;
  backdrop-filter: blur(15px);
  border: 2px solid var(--accent-gold);
  border-radius: var(--border-radius);
  color: var(--text-light);
  overflow: hidden;
}

.type-btn {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1.25rem 2rem;
  background: rgba(26, 29, 41, 0.8);
  border: none;
  border-right: 1px solid var(--border-color);
  border-radius: 0;
  color: var(--text-light);
  font-size: 1rem;
  font-weight: 600;
  font-family: 'Rajdhani', sans-serif;
  cursor: pointer;
  transition: var(--transition);
  min-width: 140px;
  justify-content: center;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  position: relative;
  height: 60px;
  white-space: nowrap;
  flex: 1;
}

.type-btn:last-child {
  border-right: none;
}

.type-btn::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 3px;
  background: var(--accent-gold);
  transition: width 0.3s ease;
  z-index: 2;
}

.type-btn:hover::before {
  width: 100%;
}

.type-btn:hover {
  background: var(--secondary-bg);
  color: var(--accent-gold);
  z-index: 1;
}

.type-btn.active {
  background: var(--accent-gold);
  color: var(--dark-bg);
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.type-btn.active::before {
  width: 100%;
  background: var(--dark-bg);
}

.type-btn svg {
  font-size: 1.2rem;
}

/* Professional Quick Links */
.quick-links {
  display: flex;
  gap: 2rem;
  justify-content: center;
  flex-wrap: wrap;
}

.quick-link {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1.5rem 2.5rem;
  background-color: #0f1a2c00 !important;
  backdrop-filter: blur(15px);
  border: 2px solid var(--accent-gold);
  border-radius: var(--border-radius);
  color: var(--text-light);
  text-decoration: none;
  font-weight: 600;
  font-size: 1.1rem;
  font-family: 'Rajdhani', sans-serif;
  transition: var(--transition);
  min-width: 200px;
  justify-content: center;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  position: relative;
  overflow: hidden;
}

.quick-link::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(212, 175, 55, 0.2), 
    transparent);
  transition: left 0.5s ease;
}

.quick-link:hover::before {
  left: 100%;
}

.quick-link:hover {
  background: var(--accent-gold);
  border-color: var(--accent-gold);
  transform: translateY(-3px);
  box-shadow: var(--shadow-heavy);
}

.quick-link svg {
  font-size: 1.3rem;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .info-slider {
    width: 300px;
  }
  
  .info-slider.left {
    left: 1rem;
  }
  
  .info-slider.right {
    right: 1rem;
  }
}

/* Tablet Design */
@media (max-width: 768px) {
  .home::before,
  .home::after {
    width: 4px;
  }
  
  .hero {
    padding: 1rem;
    background-attachment: scroll;
    min-height: 100vh;
    align-items: flex-start;
    padding-top: 2rem;
  }
  
  .hero-content {
    margin-top: 10vh;
    padding: 1rem;
    width: 100%;
    max-width: 100%;
  }
  
  .hero-content h1 {
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
  }
  
  .hero-content p {
    font-size: 1.2rem;
  }
  
  .info-slider {
    position: static;
    width: calc(100% - 2rem);
    margin: 1rem;
    transform: none;
    animation: fadeInUp 1s ease-out;
    z-index: 10;
    background: rgba(37, 40, 57, 0.8);
    border: 2px solid var(--accent-gold);
  }
  
  .info-slider.left,
  .info-slider.right {
    position: static;
    left: auto;
    right: auto;
    margin-bottom: 2rem;
  }
  
  .search-form {
    padding: 2rem 1rem;
    margin: 0;
    background: rgba(26, 29, 41, 0.9);
    backdrop-filter: blur(15px);
    border: 2px solid var(--accent-gold);
    border-radius: var(--border-radius);
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .search-container {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    order: 1;
  }
  
  .quick-links {
    order: 2;
  }
  
  .search-type-buttons {
    width: 100%;
    flex-direction: row;
    border-radius: var(--border-radius);
  }
  
  .type-btn {
    flex: 1;
    min-width: 100px;
    padding: 1rem;
    font-size: 0.9rem;
  }
  
  .quick-links {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    padding: 0 1rem;
  }
  
  .quick-link {
    width: 100%;
    max-width: 300px;
  }
}

/* Mobile Phone Design - Fixed overlapping issues */
@media (max-width: 480px) {
  /* Hide sliders completely on phones */
  .info-slider {
    display: none !important;
  }
  
  .home::before,
  .home::after {
    display: none;
  }
  
  .hero {
    padding: 1rem 0.5rem;
    background-attachment: scroll;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-top: 10rem;
  }
  
  .hero-content {
    margin-top: 0 !important;
    padding: 1rem 0.5rem;
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    position: static;
    background: transparent;
    backdrop-filter: none;
  }
  
  .hero-content h1 {
    font-size: 2rem;
    margin-bottom: 1rem;
    line-height: 1.2;
    padding: 0 1rem;
  }
  
  .hero-content p {
    font-size: 1rem;
    margin-bottom: 2rem;
    line-height: 1.5;
    padding: 0 1rem;
  }
  
  /* Fixed search form for mobile */
  .search-form {
    padding: 1.5rem 1rem;
    margin: 1rem 0.5rem 2rem 0.5rem;
    background: rgba(26, 29, 41, 0.95);
    backdrop-filter: blur(15px);
    border: 2px solid var(--accent-gold);
    border-radius: var(--border-radius);
    width: calc(100% - 1rem);
    max-width: calc(100% - 1rem);
    box-sizing: border-box;
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .search-container {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    margin-bottom: 0;
    width: 100%;
    order: 1;
  }
  
  .search-input-wrapper {
    width: 100%;
  }
  
  .search-input-wrapper input {
    padding: 1rem 1rem 1rem 3rem;
    font-size: 1rem;
    height: 50px;
    border-width: 1px;
    width: 100%;
    box-sizing: border-box;
  }
  
  .search-icon {
    left: 1rem;
    font-size: 1rem;
  }
  
  /* Fixed search type buttons */
  .search-type-buttons {
    display: flex;
    flex-direction: row;
    gap: 0;
    width: 100%;
    background: rgba(26, 29, 41, 0.8);
    border: 1px solid var(--accent-gold);
    border-radius: var(--border-radius);
    overflow: hidden;
  }
  
  .type-btn {
    width: 50%;
    min-width: auto;
    padding: 0.8rem 0.5rem;
    font-size: 0.85rem;
    height: 45px;
    border-right: 1px solid var(--border-color);
    border-bottom: none;
    gap: 0.5rem;
    flex: 1;
  }
  
  .type-btn:last-child {
    border-right: none;
    border-bottom: none;
  }
  
  .type-btn svg {
    font-size: 1rem;
  }
  
  /* Fixed quick links for mobile */
  .quick-links {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    padding: 0 1rem;
    margin-top: 0;
    width: 100%;
    max-width: 100%;
    order: 2;
  }
  
  .quick-link {
    width: calc(100% - 1rem);
    max-width: calc(100% - 1rem);
    padding: 1.2rem 1.5rem;
    font-size: 1rem;
    min-width: auto;
    gap: 0.8rem;
    margin-bottom: 0.5rem;
    box-sizing: border-box;
  }
  
  .quick-link svg {
    font-size: 1.1rem;
  }
  
  /* Ensure no horizontal scroll */
  body {
    overflow-x: hidden;
    width: 100%;
  }
  
  .home {
    overflow-x: hidden;
    width: 100%;
  }
}

/* Extra small screens (iPhone SE, small Android phones) */
@media (max-width: 375px) {
  .hero {
    padding: 0.5rem;
    padding-top: 1rem;
  }
  
  .hero-content {
    padding: 1rem 0.5rem;
    width: 100%;
  }
  
  .hero-content h1 {
    font-size: 1.8rem;
    padding: 0 0.5rem;
  }
  
  .hero-content p {
    font-size: 0.95rem;
    margin-bottom: 2rem;
    padding: 0 0.5rem;
  }
  
  .search-form {
    padding: 1.2rem 0.8rem;
    margin: 1rem 0.25rem 1.5rem 0.25rem;
    width: calc(100% - 0.5rem);
  }
  
  .search-input-wrapper input {
    padding: 1rem 1rem 1rem 2.8rem;
    font-size: 0.9rem;
    height: 48px;
  }
  
  .search-icon {
    left: 0.8rem;
    font-size: 0.9rem;
  }
  
  .type-btn {
    padding: 0.8rem 0.5rem;
    font-size: 0.8rem;
    height: 42px;
  }
  
  .quick-link {
    padding: 1rem 1.2rem;
    font-size: 0.9rem;
    width: calc(100% - 0.5rem);
  }
  
  .quick-links {
    padding: 0 0.5rem;
  }
}

/* Print styles */
@media print {
  .home::before,
  .home::after,
  .info-slider {
    display: none;
  }
  
  .hero {
    background: white;
  }
  
  .search-form {
    border: 2px solid #ccc;
  }
}