/* AboutUs.css - Professional Truck Industry Design */

/* Import industrial fonts */
@import url('https://fonts.googleapis.com/css2?family=Rajdhani:wght@300;400;500;600;700&family=Orbitron:wght@400;500;600;700;900&display=swap');

/* CSS Variables - Matching Home.css */
:root {
  --primary-bg: #1a1d29;
  --secondary-bg: #252839;
  --dark-bg: #1a1a1a;
  --accent-gold: #d4af37;
  --accent-gold-dark: #b8941f;
  --accent-gold-light: rgba(212, 175, 55, 0.1);
  --text-dark: #1a1a1a;
  --text-light: #ffffff;
  --text-muted: #9ca3af;
  --border-color: #374151;
  --shadow-light: 0 4px 20px rgba(0, 0, 0, 0.08);
  --shadow-medium: 0 8px 30px rgba(0, 0, 0, 0.12);
  --shadow-heavy: 0 12px 40px rgba(0, 0, 0, 0.15);
  --border-radius: 12px;
  --border-radius-lg: 20px;
  --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Animation keyframes matching Home.css */
@keyframes enginePulse {
  0%, 100% { opacity: 0.3; transform: scale(1); }
  50% { opacity: 0.6; transform: scale(1.05); }
}

/* Page wrapper with industrial background */
.page-wrapper {
  min-height: 100vh;
  background: var(--primary-bg);
  font-family: 'Rajdhani', sans-serif;
  color: var(--text-light);
  position: relative;
  overflow-x: hidden;
}

/* Page header styling */
.page-header {
  text-align: center;
  padding: 120px 2rem 60px;
  background: 
    linear-gradient(135deg, rgba(26, 29, 41, 0.4) 0%, rgba(26, 29, 41, 0.3) 50%, rgba(26, 29, 41, 0.4) 100%),
    url('../pages/assets/volvo.jpg') center center/cover no-repeat;
  background-attachment: fixed;
  position: relative;
}

.page-header h1 {
  font-family: 'Orbitron', monospace;
  font-size: 3.5rem;
  font-weight: 700;
  color: var(--text-light);
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
  background: linear-gradient(135deg, var(--text-light) 0%, var(--accent-gold) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.page-header p {
  font-size: 1.3rem;
  color: var(--text-light);
  max-width: 600px;
  margin: 0 auto;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
  font-weight: 400;
}

/* Main content container */
.aboutus-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem 80px;
  position: relative;
  z-index: 2;
}

/* Section styling */
.about-section {
  margin-bottom: 4rem;
  padding: 3rem;
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(212, 175, 55, 0.3);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-medium);
  position: relative;
  overflow: hidden;
  transition: var(--transition);
}

/* Golden accent line on top of sections */
.about-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    var(--accent-gold) 50%, 
    transparent 100%);
  border-radius: var(--border-radius-lg) var(--border-radius-lg) 0 0;
}

.about-section:hover {
  transform: translateY(-5px);
  background: rgba(37, 40, 57, 0.7);
  box-shadow: var(--shadow-heavy);
  border-color: rgba(212, 175, 55, 0.5);
}

/* Typography styling */
.aboutus-page h2 {
  font-family: 'Orbitron', monospace;
  font-size: 2.2rem;
  color: var(--accent-gold);
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  padding-bottom: 1rem;
}

.aboutus-page h2::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 3px;
  background: var(--accent-gold);
  border-radius: 2px;
}

.aboutus-page h3 {
  font-family: 'Orbitron', monospace;
  font-size: 1.6rem;
  color: var(--text-light);
  margin-bottom: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-weight: 600;
}

.aboutus-page p {
  font-size: 1.1rem;
  color: var(--text-light);
  line-height: 1.8;
  margin-bottom: 1.5rem;
  font-weight: 400;
}

/* Features grid styling */
.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.feature-item {
  padding: 2rem;
  background: rgba(26, 29, 41, 0.8);
  border: 2px solid rgba(212, 175, 55, 0.3);
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-light);
  transition: var(--transition);
  position: relative;
  overflow: hidden;
}

.feature-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(212, 175, 55, 0.1), 
    transparent);
  transition: left 0.6s ease;
}

.feature-item:hover::before {
  left: 100%;
}

.feature-item:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-heavy);
  border-color: var(--accent-gold);
  background: rgba(26, 29, 41, 0.9);
}

.feature-item h3 {
  margin-top: 0;
  color: var(--accent-gold);
  font-size: 1.3rem;
  margin-bottom: 1rem;
  position: relative;
  z-index: 2;
}

.feature-item p {
  margin-bottom: 0;
  color: var(--text-light);
  position: relative;
  z-index: 2;
  font-size: 1rem;
}

/* Link styling */
.aboutus-page a {
  color: var(--accent-gold);
  text-decoration: none;
  font-weight: 600;
  transition: var(--transition);
  position: relative;
}

.aboutus-page a::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--accent-gold);
  transition: width 0.3s ease;
}

.aboutus-page a:hover::after {
  width: 100%;
}

.aboutus-page a:hover {
  color: var(--accent-gold-dark);
  text-shadow: 0 0 8px rgba(212, 175, 55, 0.4);
}

/* Stats or highlight boxes */
.highlight-box {
  background: rgba(212, 175, 55, 0.15);
  padding: 2rem;
  border-radius: var(--border-radius);
  margin: 2rem 0;
  text-align: center;
  border: 1px solid rgba(212, 175, 55, 0.3);
  position: relative;
}

.highlight-box .stat-number {
  font-family: 'Orbitron', monospace;
  font-size: 3rem;
  font-weight: 700;
  color: var(--accent-gold);
  display: block;
  margin-bottom: 0.5rem;
}

.highlight-box .stat-label {
  color: var(--text-light);
  font-size: 1.2rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Team member cards (if needed) */
.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.team-member {
  background: rgba(37, 40, 57, 0.6);
  border: 1px solid rgba(212, 175, 55, 0.3);
  border-radius: var(--border-radius);
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}

.team-member:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-medium);
  border-color: var(--accent-gold);
}

.team-member img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 1rem;
  border: 3px solid var(--accent-gold);
}

.team-member h4 {
  color: var(--accent-gold);
  font-family: 'Orbitron', monospace;
  margin-bottom: 0.5rem;
}

.team-member .role {
  color: var(--text-muted);
  font-style: italic;
  margin-bottom: 1rem;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .page-wrapper::before,
  .page-wrapper::after {
    width: 6px;
  }
}

@media (max-width: 768px) {
  .page-wrapper::before,
  .page-wrapper::after {
    width: 4px;
  }
  
  .page-header {
    padding: 100px 1rem 40px;
    background-attachment: scroll;
  }
  
  .page-header h1 {
    font-size: 2.5rem;
  }
  
  .page-header p {
    font-size: 1.1rem;
  }
  
  .aboutus-page {
    padding: 0 1rem 60px;
  }
  
  .about-section {
    padding: 2rem;
    margin-bottom: 2rem;
  }
  
  .aboutus-page h2 {
    font-size: 1.8rem;
  }
  
  .aboutus-page h3 {
    font-size: 1.4rem;
  }
  
  .features-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .feature-item {
    padding: 1.5rem;
  }
  
  .team-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .page-header {
    padding: 80px 1rem 30px;
  }
  
  .page-header h1 {
    font-size: 2rem;
  }
  
  .about-section {
    padding: 1.5rem;
  }
  
  .aboutus-page h2 {
    font-size: 1.5rem;
  }
  
  .aboutus-page p {
    font-size: 1rem;
  }
  
  .feature-item {
    padding: 1.2rem;
  }
  
  .highlight-box .stat-number {
    font-size: 2.5rem;
  }
}

/* Print styles */
@media print {
  .page-wrapper::before,
  .page-wrapper::after {
    display: none;
  }
  
  .page-header {
    background: white;
    color: black;
  }
  
  .about-section {
    border: 2px solid #ccc;
    background: white;
  }
}