/* Plans.css - Professional Truck Industry Design */

/* Import industrial fonts */
@import url('https://fonts.googleapis.com/css2?family=Rajdhani:wght@300;400;500;600;700&family=Orbitron:wght@400;500;600;700;900&display=swap');

/* CSS Variables - Matching Design System */
:root {
  --primary-bg: #1a1d29;
  --secondary-bg: #252839;
  --dark-bg: #1a1a1a;
  --accent-gold: #d4af37;
  --accent-gold-dark: #b8941f;
  --accent-gold-light: rgba(212, 175, 55, 0.1);
  --text-dark: #1a1a1a;
  --text-light: #ffffff;
  --text-muted: #9ca3af;
  --border-color: #374151;
  --shadow-light: 0 4px 20px rgba(0, 0, 0, 0.08);
  --shadow-medium: 0 8px 30px rgba(0, 0, 0, 0.12);
  --shadow-heavy: 0 12px 40px rgba(0, 0, 0, 0.15);
  --border-radius: 12px;
  --border-radius-lg: 20px;
  --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Animation keyframes */
@keyframes enginePulse {
  0%, 100% { opacity: 0.3; transform: scale(1); }
  50% { opacity: 0.6; transform: scale(1.05); }
}

/* Global background setup */
body, html, #root, .App, main, div {
  background-color: transparent !important;
}

body, html {
  overflow-x: hidden;
  min-height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Rajdhani', sans-serif;
}

#root, .App, main {
  min-height: 100vh;
  background: 
    linear-gradient(135deg, rgba(26, 29, 41, 0.4) 0%, rgba(26, 29, 41, 0.3) 50%, rgba(26, 29, 41, 0.4) 100%),
    url('../pages/assets/volvo.jpg') center center/cover no-repeat;
  background-attachment: fixed;
  position: relative;
}

/* Main container */
main {
  padding-top: 90px;
}

.plans-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 60px 20px;
  min-height: 100vh;
  color: var(--text-light);
  position: relative;
  width: 100%;
  z-index: 2;
}

/* Plans header */
.plans-header {
  text-align: center;
  margin-bottom: 50px;
  padding: 40px 20px;
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(212, 175, 55, 0.3);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-medium);
  position: relative;
  overflow: hidden;
}

/* Golden accent line on top */
.plans-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    var(--accent-gold) 50%, 
    transparent 100%);
  border-radius: var(--border-radius-lg) var(--border-radius-lg) 0 0;
}

.plans-header h1 {
  font-family: 'Orbitron', monospace;
  font-size: 3rem;
  font-weight: 700;
  color: var(--text-light);
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, var(--text-light) 0%, var(--accent-gold) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.plans-header p {
  font-size: 1.2rem;
  color: var(--text-light);
  max-width: 600px;
  margin: 0 auto 30px;
  font-weight: 400;
}

/* Error message styling */
.error-message {
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(220, 38, 38, 0.2);
  border: 1px solid rgba(220, 38, 38, 0.4);
  color: #ff6b6b;
  padding: 12px 20px;
  border-radius: var(--border-radius);
  margin: 20px auto;
  max-width: 500px;
  backdrop-filter: blur(10px);
}

.error-message svg {
  margin-right: 8px;
  font-size: 18px;
}

/* Period toggle */
.period-toggle {
  display: flex;
  justify-content: center;
  background: rgba(26, 29, 41, 0.8);
  border: 2px solid var(--accent-gold);
  border-radius: 30px;
  padding: 4px;
  max-width: 280px;
  margin: 0 auto 40px;
  overflow: hidden;
}

.period-toggle button {
  padding: 12px 20px;
  border: none;
  background: transparent;
  border-radius: 25px;
  font-size: 1rem;
  font-weight: 600;
  font-family: 'Rajdhani', sans-serif;
  cursor: pointer;
  transition: var(--transition);
  color: var(--text-light);
  flex: 1;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.period-toggle button.active {
  background: var(--accent-gold);
  color: var(--dark-bg);
  box-shadow: 0 4px 15px rgba(212, 175, 55, 0.3);
}

.period-toggle button:hover:not(.active) {
  background: rgba(212, 175, 55, 0.1);
  color: var(--accent-gold);
}

/* Plans grid - FIXED FOR SINGLE ROW */
.plans-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 20px;
  margin-bottom: 50px;
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
  overflow-x: auto;
}

/* Force single row on larger screens */
@media (min-width: 1200px) {
  .plans-grid {
    grid-template-columns: repeat(4, 1fr);
    gap: 25px;
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  .plans-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }
}

/* Plan card styling - MADE SMALLER */
.plan-card {
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(212, 175, 55, 0.3);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-medium);
  overflow: hidden;
  transition: var(--transition);
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 100%;
  min-width: 240px;
  max-width: 320px;
}

.plan-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(212, 175, 55, 0.1), 
    transparent);
  transition: left 0.6s ease;
  z-index: 1;
}

.plan-card:hover::before {
  left: 100%;
}

.plan-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-heavy);
  border-color: var(--accent-gold);
  background: rgba(37, 40, 57, 0.8);
}

.plan-card.highlighted {
  border: 3px solid var(--accent-gold);
  transform: scale(1.02);
  box-shadow: 0 0 30px rgba(212, 175, 55, 0.3);
}

.plan-card.highlighted:hover {
  transform: scale(1.02) translateY(-8px);
}

/* Badge styling - MADE SMALLER */
.popular-badge {
  position: absolute;
  top: 12px;
  right: 12px;
  background: var(--accent-gold);
  color: var(--dark-bg);
  font-size: 0.7rem;
  font-weight: 700;
  font-family: 'Orbitron', monospace;
  padding: 4px 10px;
  border-radius: 15px;
  z-index: 10;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.savings-badge {
  position: absolute;
  top: 12px;
  left: 12px;
  background: linear-gradient(135deg, #10B981, #059669);
  color: white;
  font-size: 0.7rem;
  font-weight: 700;
  font-family: 'Orbitron', monospace;
  padding: 4px 10px;
  border-radius: 15px;
  z-index: 10;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.savings-badge-inline {
  display: block;
  width: fit-content;
  margin: 10px auto 0;
  background: linear-gradient(135deg, #10B981, #059669);
  color: white;
  font-size: 0.7rem;
  font-weight: 600;
  padding: 3px 8px;
  border-radius: 12px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Plan header - MADE SMALLER */
.plan-header {
  padding: 20px 15px 15px;
  border-bottom: 1px solid rgba(212, 175, 55, 0.3);
  text-align: center;
  background: rgba(26, 29, 41, 0.6);
  position: relative;
  z-index: 2;
}

.plan-header h2 {
  font-family: 'Orbitron', monospace;
  font-size: 1.4rem;
  font-weight: 700;
  color: var(--accent-gold);
  margin-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.plan-header p {
  color: var(--text-light);
  font-size: 0.9rem;
  margin-bottom: 15px;
  min-height: 36px;
  font-weight: 400;
  line-height: 1.4;
}

/* Plan price styling - MADE SMALLER */
.plan-price {
  display: flex;
  align-items: baseline;
  justify-content: center;
  margin-top: 15px;
  flex-wrap: wrap;
}

.plan-price .currency {
  font-family: 'Orbitron', monospace;
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--accent-gold);
}

.plan-price .amount {
  font-family: 'Orbitron', monospace;
  font-size: 2.4rem;
  font-weight: 700;
  color: var(--text-light);
  line-height: 1;
  margin: 0 4px;
}

.plan-price .period {
  font-size: 0.9rem;
  color: var(--text-muted);
  font-weight: 500;
}

/* Plan features - MADE SMALLER */
.plan-features {
  padding: 18px 15px;
  flex-grow: 1;
  background: rgba(26, 29, 41, 0.4);
  position: relative;
  z-index: 2;
}

.plan-features ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.plan-features li {
  display: flex;
  align-items: flex-start;
  margin-bottom: 12px;
  font-size: 0.9rem;
  color: var(--text-light);
  line-height: 1.4;
  font-weight: 500;
}

.plan-features li:last-child {
  margin-bottom: 0;
}

.feature-icon {
  color: var(--accent-gold);
  font-size: 1rem;
  margin-right: 10px;
  flex-shrink: 0;
  margin-top: 2px;
}

/* Subscribe button - MADE SMALLER */
.subscribe-button {
  margin: 0 15px 20px;
  padding: 12px 0;
  background: var(--accent-gold);
  color: var(--dark-bg);
  border: none;
  border-radius: var(--border-radius);
  font-weight: 700;
  font-size: 1rem;
  font-family: 'Rajdhani', sans-serif;
  cursor: pointer;
  transition: var(--transition);
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
  z-index: 2;
}

.subscribe-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(255, 255, 255, 0.2), 
    transparent);
  transition: left 0.5s ease;
}

.subscribe-button:hover::before {
  left: 100%;
}

.subscribe-button:hover {
  background: var(--accent-gold-dark);
  transform: translateY(-2px);
  box-shadow: var(--shadow-medium);
}

.subscribe-button:disabled {
  background: var(--text-muted);
  cursor: not-allowed;
  transform: none;
}

.subscribe-button:disabled:hover {
  background: var(--text-muted);
  box-shadow: none;
}

/* Payment options - MADE SMALLER */
.plan-payment-options {
  display: flex;
  justify-content: center;
  gap: 6px;
  margin-top: 12px;
  position: relative;
  z-index: 2;
}

.plan-payment-options button {
  padding: 6px 10px;
  border: 1px solid rgba(212, 175, 55, 0.3);
  background: rgba(26, 29, 41, 0.6);
  color: var(--text-light);
  border-radius: var(--border-radius);
  font-size: 0.75rem;
  font-weight: 600;
  font-family: 'Rajdhani', sans-serif;
  cursor: pointer;
  transition: var(--transition);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.plan-payment-options button.active {
  border-color: var(--accent-gold);
  background: rgba(212, 175, 55, 0.2);
  color: var(--accent-gold);
}

.plan-payment-options button:hover {
  border-color: var(--accent-gold);
  background: rgba(212, 175, 55, 0.1);
  color: var(--accent-gold);
}

/* Solo shot options - MADE SMALLER */
.solo-shot-options {
  margin-top: 12px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 6px;
  position: relative;
  z-index: 2;
}

/* Footer styling */
.plans-footer {
  text-align: center;
  margin-top: 50px;
  padding: 30px;
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(212, 175, 55, 0.3);
  border-radius: var(--border-radius-lg);
  color: var(--text-light);
  font-size: 1rem;
  position: relative;
}

.plans-footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    var(--accent-gold) 50%, 
    transparent 100%);
  border-radius: var(--border-radius-lg) var(--border-radius-lg) 0 0;
}

.plans-footer a {
  color: var(--accent-gold);
  text-decoration: none;
  font-weight: 600;
  transition: var(--transition);
}

.plans-footer a:hover {
  color: var(--accent-gold-dark);
  text-shadow: 0 0 8px rgba(212, 175, 55, 0.4);
}

.plans-footer p {
  margin-bottom: 10px;
}

/* Loading state */
.plans-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  color: var(--text-light);
}

.plans-loading p {
  margin-top: 16px;
  color: var(--text-light);
  font-size: 1.1rem;
}

/* Responsive Design */
@media (max-width: 991px) {
  .plans-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    max-width: 650px;
  }
  
  .plan-card {
    min-width: 220px;
    max-width: 300px;
  }
}

@media (max-width: 768px) {
  main::before,
  main::after {
    width: 4px;
  }
  
  main {
    background-attachment: scroll;
  }
  
  .plans-container {
    padding-top: 80px;
    padding-left: 15px;
    padding-right: 15px;
  }
  
  .plans-header {
    padding: 30px 20px;
    margin-bottom: 40px;
  }
  
  .plans-header h1 {
    font-size: 2.2rem;
  }
  
  .plans-header p {
    font-size: 1.1rem;
  }
  
  .plans-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
    max-width: 550px;
  }
  
  .plan-card {
    min-width: 200px;
    max-width: 260px;
  }
  
  .plan-card.highlighted {
    transform: scale(1);
    border-width: 2px;
  }
  
  .plan-card.highlighted:hover {
    transform: translateY(-5px);
  }
}

@media (max-width: 576px) {
  .plans-container {
    padding-top: 70px;
  }
  
  .plans-grid {
    grid-template-columns: 1fr;
    max-width: 100%;
    gap: 20px;
  }
  
  .plan-card {
    min-width: auto;
    max-width: 100%;
  }
  
  .plans-header h1 {
    font-size: 1.8rem;
  }
  
  .period-toggle {
    max-width: 100%;
  }
  
  .period-toggle button {
    padding: 10px 16px;
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .plan-header {
    padding: 18px 15px 12px;
  }
  
  .plan-header h2 {
    font-size: 1.3rem;
  }
  
  .plan-features {
    padding: 15px;
  }
  
  .subscribe-button {
    margin: 0 15px 18px;
    padding: 10px 0;
    font-size: 0.9rem;
  }
}

/* Print styles */
@media print {
  main::before,
  main::after {
    display: none;
  }
  
  main {
    background: white;
  }
  
  .plan-card {
    border: 2px solid #ccc;
    background: white;
  }
}