/* Contact.css - Professional Truck Industry Design */

/* Import industrial fonts */
@import url('https://fonts.googleapis.com/css2?family=Rajdhani:wght@300;400;500;600;700&family=Orbitron:wght@400;500;600;700;900&display=swap');

/* CSS Variables - Matching Plans Design System */
:root {
  --primary-bg: #1a1d29;
  --secondary-bg: #252839;
  --dark-bg: #1a1a1a;
  --accent-gold: #d4af37;
  --accent-gold-dark: #b8941f;
  --accent-gold-light: rgba(212, 175, 55, 0.1);
  --text-dark: #1a1a1a;
  --text-light: #ffffff;
  --text-muted: #9ca3af;
  --border-color: #374151;
  --shadow-light: 0 4px 20px rgba(0, 0, 0, 0.08);
  --shadow-medium: 0 8px 30px rgba(0, 0, 0, 0.12);
  --shadow-heavy: 0 12px 40px rgba(0, 0, 0, 0.15);
  --border-radius: 12px;
  --border-radius-lg: 20px;
  --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  --success: #10B981;
  --danger: #EF4444;
}

/* Animation keyframes */
@keyframes enginePulse {
  0%, 100% { opacity: 0.3; transform: scale(1); }
  50% { opacity: 0.6; transform: scale(1.05); }
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

/* Global background setup */
body, html, #root, .App, main, div {
  background-color: transparent !important;
}

body, html {
  overflow-x: hidden;
  min-height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Rajdhani', sans-serif;
}

#root, .App, main {
  min-height: 100vh;
  background: 
    linear-gradient(135deg, rgba(26, 29, 41, 0.4) 0%, rgba(26, 29, 41, 0.3) 50%, rgba(26, 29, 41, 0.4) 100%),
    url('../pages/assets/volvo.jpg') center center/cover no-repeat;
  background-attachment: fixed;
  position: relative;
}

main {
  padding-top: 90px;
}

/* Contact Page Container */
.contact-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 20px;
  min-height: 100vh;
  color: var(--text-light);
  position: relative;
  width: 100%;
  z-index: 2;
}

/* Contact Header */
.contact-header {
  text-align: center;
  margin-bottom: 50px;
  padding: 40px 20px;
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(212, 175, 55, 0.3);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-medium);
  position: relative;
  overflow: hidden;
}

/* Golden accent line on top */
.contact-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    var(--accent-gold) 50%, 
    transparent 100%);
  border-radius: var(--border-radius-lg) var(--border-radius-lg) 0 0;
}

.contact-header h1 {
  font-family: 'Orbitron', monospace;
  font-size: 3rem;
  font-weight: 700;
  color: var(--text-light);
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, var(--text-light) 0%, var(--accent-gold) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.contact-header p {
  font-size: 1.2rem;
  color: var(--text-light);
  max-width: 600px;
  margin: 0 auto;
  font-weight: 400;
  line-height: 1.6;
}

/* Contact Container */
.contact-container {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  gap: 40px;
  background: transparent;
  margin-bottom: 50px;
}

/* Contact Info Panel */
.contact-info {
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(212, 175, 55, 0.3);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-medium);
  padding: 30px;
  position: relative;
  overflow: hidden;
  transition: var(--transition);
}

.contact-info::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(212, 175, 55, 0.1), 
    transparent);
  transition: left 0.6s ease;
  z-index: 1;
}

.contact-info:hover::before {
  left: 100%;
}

.contact-info:hover {
  border-color: var(--accent-gold);
  box-shadow: var(--shadow-heavy);
}

.contact-info h2 {
  font-family: 'Orbitron', monospace;
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--accent-gold);
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  z-index: 2;
}

.contact-info h3 {
  font-family: 'Orbitron', monospace;
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--text-light);
  margin-bottom: 15px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  position: relative;
  z-index: 2;
}

.contact-info p {
  font-size: 1rem;
  color: var(--text-light);
  line-height: 1.6;
  margin-bottom: 20px;
  font-weight: 400;
  position: relative;
  z-index: 2;
}

/* Contact Methods */
.contact-methods {
  margin-top: 30px;
  position: relative;
  z-index: 2;
}

.contact-method {
  display: flex;
  align-items: flex-start;
  margin-bottom: 25px;
  padding: 15px;
  background: rgba(26, 29, 41, 0.6);
  border-radius: var(--border-radius);
  border: 1px solid rgba(212, 175, 55, 0.2);
  transition: var(--transition);
}

.contact-method:hover {
  background: rgba(26, 29, 41, 0.8);
  border-color: var(--accent-gold);
  transform: translateX(5px);
}

.method-icon {
  font-size: 1.5rem;
  margin-right: 15px;
  min-width: 30px;
  text-align: center;
  color: var(--accent-gold);
  margin-top: 2px;
}

.method-details p {
  margin: 5px 0;
  color: var(--text-light);
  font-weight: 500;
}

.method-details p:first-child {
  font-weight: 600;
  color: var(--accent-gold);
}

/* Contact Form Container */
.contact-form-container {
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(212, 175, 55, 0.3);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-medium);
  padding: 30px;
  position: relative;
  overflow: hidden;
  transition: var(--transition);
}

.contact-form-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    var(--accent-gold) 50%, 
    transparent 100%);
  border-radius: var(--border-radius-lg) var(--border-radius-lg) 0 0;
}

.contact-form-container:hover {
  border-color: var(--accent-gold);
  box-shadow: var(--shadow-heavy);
}

.contact-form-container h2 {
  font-family: 'Orbitron', monospace;
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--accent-gold);
  margin-bottom: 25px;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
}

/* Contact Form */
.contact-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

label {
  margin-bottom: 8px;
  font-weight: 600;
  color: var(--text-light);
  font-family: 'Rajdhani', sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-size: 0.9rem;
}

.required {
  color: var(--accent-gold);
  margin-left: 3px;
}

.contact-form input,
.contact-form select,
.contact-form textarea {
  width: 100%;
  padding: 15px;
  border: 2px solid rgba(212, 175, 55, 0.3);
  border-radius: var(--border-radius);
  font-size: 1rem;
  background: rgba(26, 29, 41, 0.8);
  color: var(--text-light);
  font-family: 'Rajdhani', sans-serif;
  font-weight: 500;
  transition: var(--transition);
  box-sizing: border-box;
}

.contact-form input:focus,
.contact-form select:focus,
.contact-form textarea:focus {
  border-color: var(--accent-gold);
  outline: none;
  box-shadow: 0 0 0 3px rgba(212, 175, 55, 0.1);
  background: rgba(26, 29, 41, 0.9);
}

.contact-form input::placeholder,
.contact-form textarea::placeholder {
  color: var(--text-muted);
  font-style: italic;
}

.contact-form textarea {
  height: 120px;
  resize: vertical;
  min-height: 80px;
  max-height: 200px;
}

.contact-form select {
  cursor: pointer;
}

.contact-form select option {
  background: var(--secondary-bg);
  color: var(--text-light);
}

/* Submit Button */
.contact-form button {
  background: var(--accent-gold);
  color: var(--dark-bg);
  padding: 15px 0;
  border: none;
  border-radius: var(--border-radius);
  font-size: 1.1rem;
  font-weight: 700;
  font-family: 'Rajdhani', sans-serif;
  cursor: pointer;
  transition: var(--transition);
  margin-top: 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
}

.contact-form button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent, 
    rgba(255, 255, 255, 0.2), 
    transparent);
  transition: left 0.5s ease;
}

.contact-form button:hover::before {
  left: 100%;
}

.contact-form button:hover {
  background: var(--accent-gold-dark);
  transform: translateY(-2px);
  box-shadow: var(--shadow-medium);
}

.contact-form button:disabled {
  background: var(--text-muted);
  cursor: not-allowed;
  transform: none;
}

.contact-form button:disabled:hover {
  background: var(--text-muted);
  box-shadow: none;
}

/* Error Message */
.error-message {
  background: rgba(239, 68, 68, 0.2);
  border: 1px solid rgba(239, 68, 68, 0.4);
  color: #ff6b6b;
  padding: 15px;
  border-radius: var(--border-radius);
  margin-bottom: 20px;
  font-size: 0.95rem;
  font-weight: 500;
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
}

.error-message::before {
  content: '⚠️';
  margin-right: 10px;
  font-size: 1.2rem;
}

/* Success Message */
.success-message {
  text-align: center;
  padding: 40px;
  background: rgba(37, 40, 57, 0.8);
  backdrop-filter: blur(15px);
  border: 2px solid var(--success);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-medium);
  position: relative;
  overflow: hidden;
}

.success-message::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    var(--success) 50%, 
    transparent 100%);
  border-radius: var(--border-radius-lg) var(--border-radius-lg) 0 0;
}

.success-message h2 {
  font-family: 'Orbitron', monospace;
  color: var(--success);
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 15px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.success-message p {
  color: var(--text-light);
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 25px;
}

.success-message button {
  background: var(--success);
  color: white;
  padding: 12px 25px;
  border: none;
  border-radius: var(--border-radius);
  font-size: 1rem;
  font-weight: 600;
  font-family: 'Rajdhani', sans-serif;
  cursor: pointer;
  transition: var(--transition);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.success-message button:hover {
  background: #0d9e6d;
  transform: translateY(-2px);
  box-shadow: var(--shadow-medium);
}

/* Contact Footer */
.contact-footer {
  text-align: center;
  margin-top: 50px;
  padding: 30px;
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(212, 175, 55, 0.3);
  border-radius: var(--border-radius-lg);
  color: var(--text-light);
  font-size: 1rem;
  position: relative;
}

.contact-footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    var(--accent-gold) 50%, 
    transparent 100%);
  border-radius: var(--border-radius-lg) var(--border-radius-lg) 0 0;
}

.contact-footer p {
  margin-bottom: 10px;
  font-weight: 500;
}

.contact-footer a {
  color: var(--accent-gold);
  text-decoration: none;
  font-weight: 600;
  transition: var(--transition);
}

.contact-footer a:hover {
  color: var(--accent-gold-dark);
  text-shadow: 0 0 8px rgba(212, 175, 55, 0.4);
}

/* Responsive Design */
@media (max-width: 1200px) {
  main::before,
  main::after {
    width: 6px;
  }
  
  .contact-container {
    gap: 30px;
  }
}

@media (max-width: 992px) {
  .contact-container {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .contact-info,
  .contact-form-container {
    width: 100%;
  }
  
  .form-row {
    grid-template-columns: 1fr;
    gap: 15px;
  }
}

@media (max-width: 768px) {
  main::before,
  main::after {
    width: 4px;
  }
  
  main {
    background-attachment: scroll;
  }
  
  .contact-page {
    padding: 80px 15px 60px;
  }
  
  .contact-header {
    padding: 30px 20px;
    margin-bottom: 40px;
  }
  
  .contact-header h1 {
    font-size: 2.2rem;
  }
  
  .contact-header p {
    font-size: 1.1rem;
  }
  
  .contact-info,
  .contact-form-container {
    padding: 25px 20px;
  }
  
  .contact-method {
    padding: 12px;
  }
  
  .method-icon {
    font-size: 1.3rem;
  }
}

@media (max-width: 576px) {
  .contact-page {
    padding: 70px 15px 50px;
  }
  
  .contact-header h1 {
    font-size: 1.8rem;
  }
  
  .contact-header {
    padding: 25px 15px;
  }
  
  .contact-info,
  .contact-form-container {
    padding: 20px 15px;
  }
  
  .contact-form {
    gap: 15px;
  }
  
  .contact-form input,
  .contact-form select,
  .contact-form textarea {
    padding: 12px;
  }
  
  .contact-method {
    flex-direction: column;
    text-align: center;
  }
  
  .method-icon {
    margin-right: 0;
    margin-bottom: 10px;
  }
}

@media (max-width: 480px) {
  .contact-header h1 {
    font-size: 1.6rem;
  }
  
  .contact-info h2,
  .contact-form-container h2 {
    font-size: 1.5rem;
  }
  
  .contact-form button {
    padding: 12px 0;
    font-size: 1rem;
  }
}