/* TrailerDetails.css - Professional Truck Industry Design */

/* Import industrial fonts */
@import url('https://fonts.googleapis.com/css2?family=Rajdhani:wght@300;400;500;600;700&family=Orbitron:wght@400;500;600;700;900&display=swap');

/* CSS Variables */
:root {
  --primary-bg: #1a1d29;
  --secondary-bg: #252839;
  --dark-bg: #1a1a1a;
  --accent-gold: #d4af37;
  --accent-gold-dark: #b8941f;
  --accent-gold-light: rgba(212, 175, 55, 0.1);
  --text-dark: #1a1a1a;
  --text-light: #ffffff;
  --text-muted: #9ca3af;
  --border-color: #374151;
  --success-green: #10b981;
  --warning-orange: #f59e0b;
  --danger-red: #ef4444;
  --info-blue: #3b82f6;
  --shadow-light: 0 4px 20px rgba(0, 0, 0, 0.08);
  --shadow-medium: 0 8px 30px rgba(0, 0, 0, 0.12);
  --shadow-heavy: 0 12px 40px rgba(0, 0, 0, 0.15);
  --border-radius: 12px;
  --border-radius-lg: 20px;
  --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Animations */
@keyframes fadeInUp {
  0% {
    transform: translateY(30px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes slideInLeft {
  0% { 
    transform: translateX(-100%); 
    opacity: 0; 
  }
  100% { 
    transform: translateX(0); 
    opacity: 1; 
  }
}

@keyframes slideInRight {
  0% { 
    transform: translateX(100%); 
    opacity: 0; 
  }
  100% { 
    transform: translateX(0); 
    opacity: 1; 
  }
}

@keyframes shimmer {
  0% { background-position: -200px 0; }
  100% { background-position: calc(200px + 100%) 0; }
}

@keyframes pulseGlow {
  0%, 100% { 
    box-shadow: 0 0 5px var(--accent-gold-light);
  }
  50% { 
    box-shadow: 0 0 20px var(--accent-gold), 0 0 30px var(--accent-gold-light);
  }
}

/* Main Container */
.trailer-details {
  min-height: 100vh;
  background: var(--primary-bg);
  backdrop-filter: blur(15px);
  color: var(--text-light);
  font-family: 'Rajdhani', sans-serif;
  padding: 2rem;
  position: relative;
}

.trailer-details::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 50%, rgba(212, 175, 55, 0.03) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(212, 175, 55, 0.03) 0%, transparent 50%),
    radial-gradient(circle at 40% 80%, rgba(212, 175, 55, 0.02) 0%, transparent 50%);
  pointer-events: none;
  z-index: 0;
}

.trailer-details > * {
  position: relative;
  z-index: 1;
}

/* Back Button */
.back-button {
  display: inline-flex;
  align-items: center;
  padding: 1rem 2rem;
  background: rgba(37, 40, 57, 0.8);
  backdrop-filter: blur(15px);
  border: 2px solid var(--accent-gold);
  border-radius: var(--border-radius);
  color: var(--text-light);
  text-decoration: none;
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
  font-size: 1.1rem;
  cursor: pointer;
  transition: var(--transition);
  margin-bottom: 2rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.back-button:hover {
  background: var(--accent-gold);
  color: var(--dark-bg);
  transform: translateY(-2px);
  box-shadow: var(--shadow-medium);
}

/* Title */
.trailer-details h1 {
  font-family: 'Orbitron', monospace;
  font-size: 3rem;
  font-weight: 700;
  color: var(--text-light);
  text-align: center;
  margin-bottom: 3rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  position: relative;
  animation: fadeInUp 0.8s ease-out;
}

.trailer-details h1::after {
  content: '';
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 4px;
  background: linear-gradient(90deg, transparent, var(--accent-gold), transparent);
  border-radius: 2px;
}

/* Image Gallery */
.trailer-image-gallery {
  max-width: 1200px;
  margin: 0 auto 4rem;
  position: relative;
  animation: fadeInUp 1s ease-out 0.2s both;
}

.premium-badge {
  position: absolute;
  top: 20px;
  left: 20px;
  background: linear-gradient(135deg, var(--accent-gold), var(--accent-gold-dark));
  color: var(--dark-bg);
  padding: 0.5rem 1.5rem;
  border-radius: var(--border-radius);
  font-weight: 700;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  z-index: 10;
  box-shadow: var(--shadow-medium);
  animation: pulseGlow 2s infinite;
}

.main-image-container {
  width: 100%;
  height: 500px;
  border-radius: var(--border-radius-lg);
  overflow: hidden;
  position: relative;
  background: var(--secondary-bg);
  border: 2px solid var(--border-color);
  box-shadow: var(--shadow-heavy);
}

.main-image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: var(--transition);
}

.main-image-container:hover img {
  transform: scale(1.05);
}

.thumbnail-gallery {
  display: flex;
  gap: 1rem;
  margin-top: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

.thumbnail {
  width: 100px;
  height: 80px;
  border-radius: var(--border-radius);
  overflow: hidden;
  cursor: pointer;
  border: 2px solid var(--border-color);
  transition: var(--transition);
  position: relative;
}

.thumbnail:hover {
  border-color: var(--accent-gold);
  transform: translateY(-2px);
}

.thumbnail.active {
  border-color: var(--accent-gold);
  box-shadow: 0 0 15px var(--accent-gold-light);
}

.thumbnail img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Info Container */
.trailer-info-container {
  max-width: 1400px;
  margin: 0 auto;
  animation: fadeInUp 1s ease-out 0.4s both;
}

.trailer-info-main {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 3rem;
  margin-bottom: 3rem;
}

/* Specs Grid */
.specs-grid {
  backdrop-filter: blur;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
}

.spec-card {
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  padding: 1.5rem;
  transition: var(--transition);
  position: relative;
  overflow: hidden;
}

.spec-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, var(--accent-gold), transparent);
  transition: left 0.5s ease;
}

.spec-card:hover::before {
  left: 100%;
}

.spec-card:hover {
  border-color: var(--accent-gold);
  transform: translateY(-5px);
  box-shadow: var(--shadow-medium);
}

.spec-card h3 {
  font-family: 'Orbitron', monospace;
  font-size: 1rem;
  color: var(--accent-gold);
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
}

.spec-card p {
  font-size: 1.2rem;
  color: var(--text-light);
  font-weight: 700;
  margin: 0;
}

/* Details Sidebar */
.details-sidebar {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.price-display {
  background: linear-gradient(135deg, var(--accent-gold), var(--accent-gold-dark));
  color: var(--dark-bg);
  padding: 2rem;
  border-radius: var(--border-radius-lg);
  text-align: center;
  box-shadow: var(--shadow-heavy);
  position: relative;
  overflow: hidden;
}

.price-display::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(45deg, transparent, rgba(255,255,255,0.1), transparent);
  animation: shimmer 3s infinite;
}

.price-label {
  display: block;
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.price-value {
  display: block;
  font-family: 'Orbitron', monospace;
  font-size: 2.5rem;
  font-weight: 900;
  line-height: 1;
}

.price-value::before {
  content: '$';
  font-size: 1.5rem;
  vertical-align: top;
  margin-right: 0.2rem;
}

.trailer-status {
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  padding: 1.5rem;
  text-align: center;
}

.status-label {
  display: block;
  font-size: 1rem;
  color: var(--text-muted);
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.status-badge {
  display: inline-block;
  padding: 0.5rem 1.5rem;
  border-radius: 25px;
  font-weight: 700;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.status-badge.available {
  background: var(--success-green);
  color: white;
}

.status-badge.reserved {
  background: var(--warning-orange);
  color: white;
}

.status-badge.sold {
  background: var(--danger-red);
  color: white;
}

.status-badge.pending {
  background: var(--text-muted);
  color: white;
}

/* Financing Section */
.financing-section {
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  padding: 2rem;
}

.financing-section h3 {
  font-family: 'Orbitron', monospace;
  color: var(--accent-gold);
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
}

.financing-option {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
  border-bottom: 1px solid var(--border-color);
}

.financing-option:last-child {
  border-bottom: none;
}

.financing-label {
  color: var(--text-light);
  font-weight: 600;
}

.financing-value {
  color: var(--accent-gold);
  font-weight: 700;
  font-size: 1.1rem;
}

/* Contact Section */
.contact-section {
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  padding: 2rem;
}

.contact-section h3 {
  font-family: 'Orbitron', monospace;
  color: var(--accent-gold);
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
}

.contact-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  width: 100%;
  padding: 1.25rem;
  margin-bottom: 1rem;
  background: rgba(26, 29, 41, 0.8);
  border: 2px solid var(--accent-gold);
  border-radius: var(--border-radius);
  color: var(--text-light);
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
  font-size: 1.1rem;
  cursor: pointer;
  transition: var(--transition);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  text-decoration: none;
}

.contact-button:last-child {
  margin-bottom: 0;
}

.contact-button:hover {
  background: var(--accent-gold);
  color: var(--dark-bg);
  transform: translateY(-2px);
  box-shadow: var(--shadow-medium);
}

/* Description */
.trailer-description {
  max-width: 1400px;
  margin: 0 auto 3rem;
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-lg);
  padding: 2.5rem;
  animation: fadeInUp 1s ease-out 0.6s both;
}

.trailer-description h3 {
  font-family: 'Orbitron', monospace;
  font-size: 1.5rem;
  color: var(--accent-gold);
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.trailer-description p {
  font-size: 1.2rem;
  line-height: 1.8;
  color: var(--text-light);
}

/* Features Section */
.features-section {
  max-width: 1400px;
  margin: 0 auto 3rem;
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-lg);
  padding: 2.5rem;
  animation: fadeInUp 1s ease-out 0.8s both;
}

.features-section h3 {
  font-family: 'Orbitron', monospace;
  font-size: 1.5rem;
  color: var(--accent-gold);
  margin-bottom: 2rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
}

.feature-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1.5rem;
  background: rgba(26, 29, 41, 0.5);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  transition: var(--transition);
}

.feature-item:hover {
  border-color: var(--accent-gold);
  transform: translateY(-3px);
  box-shadow: var(--shadow-light);
}

.feature-item svg {
  font-size: 1.5rem;
  color: var(--accent-gold);
  flex-shrink: 0;
}

.feature-item span {
  font-weight: 600;
  color: var(--text-light);
}

/* Details Grid */
.details-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  animation: fadeInUp 1s ease-out 1s both;
}

.detail-card {
  background: rgba(37, 40, 57, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
  position: relative;
  overflow: hidden;
}

.detail-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, transparent, var(--accent-gold), transparent);
}

.detail-card:hover {
  border-color: var(--accent-gold);
  transform: translateY(-5px);
  box-shadow: var(--shadow-medium);
}

.detail-card h3 {
  font-family: 'Orbitron', monospace;
  font-size: 1.2rem;
  color: var(--accent-gold);
  margin-bottom: 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.detail-card p {
  font-size: 1.3rem;
  color: var(--text-light);
  font-weight: 700;
  margin: 0;
}

/* Loading and Error States */
.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 50vh;
  flex-direction: column;
}

.error-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 50vh;
  text-align: center;
  gap: 2rem;
}

.error-message {
  background: rgba(239, 68, 68, 0.1);
  border: 2px solid var(--danger-red);
  border-radius: var(--border-radius-lg);
  padding: 2rem;
  max-width: 500px;
}

.error-icon {
  font-size: 3rem;
  color: var(--danger-red);
  margin-bottom: 1rem;
}

.error-message h2 {
  font-family: 'Orbitron', monospace;
  color: var(--danger-red);
  margin-bottom: 1rem;
  font-size: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.error-message p {
  color: var(--text-light);
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.retry-button {
  padding: 1rem 2rem;
  background: var(--danger-red);
  border: none;
  border-radius: var(--border-radius);
  color: white;
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
  font-size: 1.1rem;
  cursor: pointer;
  transition: var(--transition);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.retry-button:hover {
  background: #dc2626;
  transform: translateY(-2px);
}

/* Responsive Design */
@media (max-width: 1200px) {
  .trailer-info-main {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .specs-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}

@media (max-width: 768px) {
  .trailer-details {
    padding: 1rem;
  }
  
  .trailer-details h1 {
    font-size: 2rem;
    margin-bottom: 2rem;
  }
  
  .main-image-container {
    height: 300px;
  }
  
  .thumbnail-gallery {
    gap: 0.5rem;
  }
  
  .thumbnail {
    width: 80px;
    height: 60px;
  }
  
  .specs-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .spec-card {
    padding: 1rem;
  }
  
  .price-value {
    font-size: 2rem;
  }
  
  .details-sidebar {
    gap: 1.5rem;
  }
  
  .financing-section,
  .contact-section {
    padding: 1.5rem;
  }
  
  .features-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .feature-item {
    padding: 1rem;
  }
  
  .details-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .detail-card {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .trailer-details h1 {
    font-size: 1.5rem;
  }
  
  .back-button {
    padding: 0.75rem 1.5rem;
    font-size: 1rem;
  }
  
  .main-image-container {
    height: 250px;
  }
  
  .price-value {
    font-size: 1.8rem;
  }
  
  .contact-button {
    padding: 1rem;
    font-size: 1rem;
  }
  
  .trailer-description,
  .features-section {
    padding: 1.5rem;
  }
}

/* Print Styles */
@media print {
  .trailer-details::before,
  .back-button,
  .contact-section {
    display: none;
  }
  
  .trailer-details {
    background: white;
    color: black;
  }
  
  .spec-card,
  .detail-card,
  .trailer-description,
  .features-section {
    background: white;
    border: 1px solid #ccc;
  }
  
  .price-display {
    background: #f5f5f5;
    color: black;
  }
}